"use client";

import React from "react";
import { colors, radius } from "@ds/tokens";

interface ButtonProps extends React.ButtonHTMLAttributes<HTMLButtonElement> {
    variant?: "primary" | "secondary" | "tertiary" | "error" | "success" | "warning";
    size?: "sm" | "md" | "lg";
}

export const Button: React.FC<ButtonProps> = ({
    variant = "primary",
    size = "md",
    children,
    ...props
}) => {
    const [isHovered, setIsHovered] = React.useState(false);


    return (
        <button
            {...props}
            onMouseEnter={() => setIsHovered(true)}
            onMouseLeave={() => setIsHovered(false)}
            style={{
                background: isHovered ? backgroundHover : background,
                border: "none",
                color: isHovered ? colorHover : color,
                borderRadius: radius.md,
                padding: "8px 16px",
                cursor: "pointer",
                textDecoration:
                    variant === "tertiary" && isHovered ? "underline" : "none"
            }}
        >
            {children}
        </button>
    );
};
